import { useState } from 'react';
import { OptionsGeneric } from '@popperjs/core';
import { usePopper } from 'react-popper';

export interface WrappedPopperOptions<TModifier> extends Partial<OptionsGeneric<TModifier>> {
  arrow?: boolean;
  offset?: [number, number];
}

export function usePopperWrapper<T extends WrappedPopperOptions<any>>(options: T) {
  const { modifiers = [], arrow, offset = [0, 10], ...otherOptions } = options;

  const [anchorElement, setAnchorElement] = useState<HTMLElement | null>(null);
  const [popperElement, setPopperElement] = useState<HTMLElement | null>(null);
  const [arrowElement, setArrowElement] = useState<HTMLElement | null>(null);

  const { styles, attributes, update, forceUpdate, state } = usePopper(anchorElement, popperElement, {
    modifiers: [
      { name: 'offset', enabled: true, options: { offset } },
      arrow && { name: 'arrow', options: { element: arrowElement } },
      ...modifiers,
    ].filter(Boolean),
    ...otherOptions,
  });

  const anchorProps = { ref: setAnchorElement };
  const contentProps = { ref: setPopperElement, style: styles.popper, ...attributes.popper };
  const arrowProps = { ref: setArrowElement, style: styles.arrow };

  return { anchorProps, contentProps, arrowProps, update, forceUpdate, state };
}

export type PopperWrapper = ReturnType<typeof usePopperWrapper>;
